html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  color: #ffffff;
  line-height: 1;
  margin: 0;
  padding: 0;
  font-family: "Gilroy", sans-serif;
  min-height: 100vh;
  transition: ease-in-out 0.25s;
  overflow-x: hidden;
  background-color: #212121;
  background-image: url("../../assets/img/bg.png");
  background-size: 100%;
}

.logo {
  display: inline-block;
  height: auto;
}

.logo > img {
  object-fit: contain;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  visibility: hidden;
  opacity: 0;
}

.visually-visible {
  visibility: visible;
  opacity: 1;
}

