    transform: scaleY(0);
    transform-origin: 0 0;
    transition: ease-in-out 0.25s;
    justify-content: center;
    align-items: center;
    gap: 20px;
    position: absolute;
    top: 99px;
    background-color: #19151f;
    left: 0;
    right: 0;
    box-shadow: 0 4px 4px rgba(0, 99, 32, 0.15);
    visibility: hidden;
  }

  .nav-menu--opened {
    transform: scaleY(1);
    transform-origin: 0 0;
    transition: ease-in-out 0.25s;
    padding-top: 32px;
    padding-bottom: 44px;
    visibility: visible;
  }
}
