}
.hero {
  min-height: 100vh;
  position: relative;
}

.hero__container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero__content {
  display: flex;
  align-items: center;
  column-gap: 54px;
}

