  display: flex;
  column-gap: 40px;
}

.hero__list-item {
  color: rgba(255, 255, 255, 0.85);
  line-height: 32px;
  position: relative;
  padding-left: 32px;
}

.hero__list-item::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #ebb715;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.download {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  width: 187px;
  height: 187px;
  background-color: #ebb715;
  box-shadow: 0px 4px 20px rgba(235, 183, 21, 0.25);
  border-radius: 50%;
  position: relative;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
  padding: 60px 44px;
  transition: ease-in-out 0.25s;
}

.download::before {
  content: "";
  position: absolute;
  display: block;
  width: 130%;
  height: 130%;
  animation: spinning 25s infinite linear;
  background-image: url("data:image/svg+xml,%3csvg width='100%' height='100%' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='250' ry='250' stroke='%23EBB715FF' stroke-width='2' stroke-dasharray='24' stroke-dashoffset='20' stroke-linecap='butt'/%3e%3c/svg%3e");
}

.download::after {
  content: url("../../assets/img/gift-min.png");
  position: absolute;
  display: block;
  right: 0;
  bottom: 0;
  transform: translate(21%, 35%);
}

.download:hover {
  transition: ease-in-out 0.25s;
  animation: 0.5s infinite alternate scaling;
}

@keyframes spinning {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(180deg);
  }
}
@keyframes scaling {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.03);
  }
}
@media (max-width: 767px) {
  .hero__subtitle {
    line-height: 21px;
  }

  .download {
    width: 150px;
