.video-player {
  display: none;
  object-fit: cover;
}

.video-wrapper.show-video .video-player {
  display: block;
}

.video-wrapper.show-video .video-thumbnail {
  display: none;
}

.video-wrapper.visually-hidden {
  display: none;
  position: absolute;
  width: 0;
  height: 0;
}

